import { Component, h, Prop, State } from '@stencil/core';
import { IDocument } from '../../types/Document';
import { LimeWebComponentPlatform } from '@limetech/lime-web-components';
import { ISession } from '../../types/Session';

@Component({
    tag: 'layout-overview',
    shadow: true,
    styleUrl: 'layout-overview.scss',
})
export class LayoutOverview {
    @Prop() public sentDocuments: IDocument;
    @Prop() public platform: LimeWebComponentPlatform;
    @Prop() public externalId: string;
    @Prop() public session: ISession;
    @Prop() public documents: IDocument[] = [];
    @Prop() public isAside: boolean;

    @State() private isLoadingDocuments: boolean;

    public render() {
        return [
            <div class={{ 'main-layout': true, aside: this.isAside }}>
                <div class="send-new-document-container">
                    <h3>Send new document</h3>
                    <div class="send-new-document-buttons">
                        <send-new-document-button isSigning={true} />
                        <send-new-document-button isSigning={false} />
                    </div>
                </div>
                <div class="related-documents">
                    <h3>Related documents</h3>
                    {this.isLoadingDocuments ? (
                        <ga-loader />
                    ) : (
                        <document-list documents={this.documents} />
                    )}
                </div>
            </div>,
        ];
    }
}
